
































import { Component, Vue } from "vue-property-decorator";
import { State, Getter, Action, Mutation, namespace } from "vuex-class";

@Component
export default class extends Vue {
  created() {}
  @Getter("words") words: any;
  @State("dictionary") dictionary: any;

  get longInfoList() {
    return [
      {
        icon: ["fas", "home"],
        msg: this.words.address1,
      },
      {
        icon: ["fas", "home"],
        msg: this.words.address2,
      },
    ];
  }

  get shortInfoList() {
    return [
      {
        icon: ["fas", "envelope"],
        msg: this.words.email,
      },
      {
        icon: ["fas", "phone-alt"],
        msg: this.words.phone,
      },
      {
        icon: ["fas", "tv"],
        msg: this.words.web,
      },
    ];
  }

  setColor(item:any) {
    item.hovTheme = item.theme;
    if(item.hovTheme === '#fff') {
      item.bgC = '#000';
    }
  }

  resetColor(item:any) {
    item.hovTheme = '#000';
    item.bgC = '#fff';
  }

  icons = [
    {
      hovTheme: '#000',
      icon:　['fab', 'facebook-square'],
      theme: 'rgb(59, 87, 157)',
    },
    {
      hovTheme: '#000',
      icon:　['fab', 'github-square'],
      theme: '#fff',
      bgC: '#fff',
    },
    {
      hovTheme: '#000',
      icon:　['fab', 'linkedin'],
      theme: 'rgb(24, 119, 242)',
    },
    {
      hovTheme: '#000',
      icon:　['fab', 'twitter-square'],
      theme: 'rgb(59, 200, 244)',
    },
  ]
}
